@import '~animate.css/animate.css';
@import './v-tooltip';

@import './helpers';
@import './app';

[v-cloak] {
	display: none;
}

.animated {
	animation-duration: 0.5s;
}

.button.is-naked {
	background: 0;
	border: none;
}

.icon.angle {
	transition: transform 0.3s ease;

	&[aria-hidden='true'] {
		transform: rotate(180deg);
	}

	&[aria-expanded='true'] {
		transform: rotate(-180deg);
	}
}

@media screen and (max-width: 769px) {
	.columns.is-reverse-mobile {
		flex-direction: column-reverse;
		display: flex;
		.column {
			width: 100%;
		}
	}
}

.tag.is-table-tag {
	font-size: 0.8rem;
	font-weight: bold;
	height: 1.4em;
	padding: 7px;
}



